import { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import Card from "../../components/card/card"
import './nested.scss'

function Nested() {

    const [render, setRender] = useState([])
    useEffect(() => {
        fetch("https://fakestoreapi.com/products")
            .then((response) => response.json())
            .then((res) => setRender(res))
    })
    return (
        <>
            <section className="nested">
                <div className="nested__wrapper container">

                    <div className="nested__wrapper">
                        <ul className="nested__list">
                            <li className="nested__hi">
                                {
                                    render?.slice(16, 20).map(Toxirrrr => (
                                        <Link to={`/product/${Toxirrrr.id}`}>
                                            <Card
                                                id={Toxirrrr.id}
                                                img={Toxirrrr.image}
                                                time='Вчера 22:55'
                                                name={Toxirrrr.title}
                                                price={Toxirrrr.price}
                                            />
                                        </Link>
                                    ))
                                }
                            </li>
                        </ul>
                    </div>
                </div>
            </section>
        </>
    )


}
export default Nested;