import { AuthContext } from './../../components/authContext'
import { Link, useNavigate } from 'react-router-dom'
import { useContext, useRef } from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import './login.scss'
import login_svg from './../../assets/img/login__svg-r.svg'


export function login() {

    const nameRef = useRef()
    const passwordRef = useRef()
    const textRef = useRef()

    const { token } = useContext(AuthContext)

    const navigate = useNavigate()


    token ? (
        setTimeout(function () {
            navigate('/')
        }, 2000)
    ) : (
        textRef.textContent = ('Error')
    )

    const { setToken } = useContext(AuthContext)

    const validationScheme = Yup.object().shape({
        name: Yup.string().required('ismingizni kiriting'),
        password: Yup.string().min(5)
    })

    const initialValues = {
        name: '',
        password: ''
    }

    const formik = useFormik({
        initialValues,
        validationScheme,
        onSubmit: (values) => {
            console.log(values);
        }
    })



    const handleSubmit = (e) => {
        e.preventDefault();

        fetch('https://reqres.in/api/login', {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify({ email: nameRef.current.value, password: passwordRef.current.value })
        }).then(res => res.json())
            .then(data => {
                JSON.stringify(setToken(data.token))
                
                token ? (
                    navigate('/')
                ) : (
                    textRef.current.textContent = ('Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер') 
                )

            })
            .catch(error => console.log(data.error))
            .finally()
    }
    return (
        <>
            <section className='login'>
                <div className="login__wrapper container">
                    <div className="login__top">

                    </div>
                    <div className="login__bottom">
                        <div className='login__left'>

                            <h2 className='login__header'>Ваш вход завершена!</h2>


                            {
                                token ? (
                                    <p ref={textRef}>Вы успешно зарегистрированы на сайте киви и ваше пароль отправлен в виде смс на ваше телефонный номер</p>
                                ) : (
                                    <p ref={textRef}>Пожалуйста введите логин и пароль</p>
                                )
                            }

                            <form
                                onSubmit={handleSubmit}
                                onChange={formik.handleSubmit}
                                className='form'
                            >
                                <label className='login__label' htmlFor="login">Введите email или номер телефона</label>

                                <input
                                    value={formik.values.name}
                                    onChange={formik.handleChange}
                                    name='name'
                                    ref={nameRef}
                                    type='email'
                                    placeholder='Login'
                                    id='login'
                                    className='login__input'
                                    required
                                />
                                <label className='login__label' htmlFor="password">Пароль</label>
                                <input
                                    value={formik.values.password}
                                    onChange={formik.handleChange}
                                    name='password'
                                    ref={passwordRef}
                                    id='password'
                                    type='password'
                                    placeholder='Password'
                                    className='login__input'
                                    required
                                />

                                <button
                                    id='btn'
                                    className='btn'
                                    onChange={formik.handleChange}
                                    type='submit'
                                >
                                    Войти
                                </button>

                                <div className='footer__span-wrapper'>
                                    <span className='login__span' />

                                    <p className='login__text'>или</p>

                                    <span className='login__span' />

                                </div>
                                <Link className='login__recovery'>Восстоновить ваше пароль</Link>



                            </form>

                            {
                                formik.errors.login && <h4 style={{ color: 'red' }}>{formik.errors.login}</h4>
                            }
                            {
                                formik.errors.password && <h4 style={{ color: 'red' }}>{formik.errors.password}</h4>
                            }

                        </div>

                        <div className="login__right">
                            <div className="login__r-wrapper">
                                <img src={login_svg} alt="kivi" />

                                <p className='login__r-text'>Откройте для себя целую плошадку для продаже</p>

                                <button type='submit' className='login__btn'>Как это делать?</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default login