function Settings() {

    function logOut() {

        setTimeout(function () {
            navigate('/Login')
        }, 2000)

        localStorage.removeItem("token")
    }

    function logOut() {
        localStorage.removeItem('token')
    }
    return (

        <>
            <div >
                <button
                    style={
                        {
                            fontFamily: "Gilroy",
                            fontStyle: 'normal',
                            fontWeight: '500',
                            fontSize: '17px',
                            lineHeight: '21px',
                            textAlign: 'center',
                            color: '#FFFFFF',
                            backgroundColor: ' #41D481',
                            borderRadius: '5px',
                            border: 'none',
                            outline: 'none',
                            padding: ' 9px 23px',
                            width: '111px',
                        }
                    }
                    onClick={logOut}
                >
                    Log Out
                </button>

            </div>
        </>
    )

}

export default Settings