import svg1 from './../../assets/icons/svg1.svg'
import svg2 from './../../assets/icons/svg2.svg'
import svg3 from './../../assets/icons/svg3.svg'
import svg4 from './../../assets/icons/svg4.svg'
import svg5 from './../../assets/icons/svg5.svg'
import svg6 from './../../assets/icons/svg6.svg'
import svg7 from './../../assets/icons/svg7.svg'
import product__input from './../../assets/img/product__item.svg'
import input__search from './../../assets/icons/svg__lupa.svg'

function HeaderCard() {
    return (
        <>

            <div

                className="product__wrapper container"

                style={

                    {
                        paddingTop: '32px',
                        paddingBottom: '29px',
                        userSelect: 'none'

                    }

                }

            >
                <div

                    className="product__input-wrapper"

                    style={

                        {

                            position: 'relative'

                        }

                    }

                >

                    <img src={input__search}

                        style={

                            {
                                position: 'absolute',
                                left: '13px',
                                top: '13px',

                            }

                        }

                        alt="input__svg"

                    />

                    <input

                        type="search"

                        style={

                            {
                                width: '1140px',
                                background: '#FFFFFF',
                                border: ' 1px solid rgba(151, 151, 151, 0.496575)',
                                borderRadius: '6px',
                                padding: '16px 20px 15px 58px'

                            }

                        }

                        className="product__input"

                    />

                    <p

                        style={

                            {

                                color: '#787993',
                                fontFamily: 'Gilroy',
                                fontStyle: 'normal',
                                fontWeight: '500',
                                fontSize: ' 16px',
                                position: 'absolute',
                                left: '60px',
                                top: '15px',
                                lineHeight: ' 19px'

                            }

                        }

                    >

                        Введите название обявления или

                        <span
                            className="product__input-span"


                            style={

                                {

                                    color: '#2979FF',
                                    fontFamily: 'Gilroy',
                                    fontStyle: 'normal',
                                    fontWeight: '500',
                                    fontSize: ' 16px',
                                    lineHeight: ' 19px',
                                    marginLeft: '10px'

                                }

                            }

                        >

                            выберите из списка

                        </span>

                    </p>

                </div>

                <div
                    className="product__img-wrapper"
                    style={
                        {
                            display: 'flex',
                            gap: '13px', paddingTop: '13px'
                        }
                    }
                >

                    <img

                        src={svg1}
                        alt="icon__svg"

                    />

                    <img

                        src={svg2}
                        alt="icon__svg"

                    />

                    <img

                        src={svg3}
                        alt="icon__svg"

                    />

                    <img

                        src={svg4}
                        alt="icon__svg"

                    />

                    <img

                        src={svg5}
                        alt="icon__svg"

                    />

                    <img

                        src={svg6}
                        alt="icon__svg"

                    />

                    <img

                        src={svg7}
                        alt="icon__svg"

                    />

                </div>

                <div

                    className="input__item wrapper"

                    style={

                        {
                            display: 'flex',
                            alignItems: 'center',
                            gap: '54px',
                            paddingTop: '25px'

                        }

                    }

                >

                    <p
                        className="input_i-text"
                    >
                        Вы сейчас в рубрике
                    </p>

                    <img
                        src={product__input}
                        alt="image"
                    />

                </div>

                <div

                    className="product__range-wrapper"

                    style={

                        {

                            display: 'flex',
                            alignItems: 'center',
                            gap: '54px',
                            paddingTop: '26px'

                        }

                    }

                >

                    <p

                        className="product__range-text"

                    >

                        Выберите цену от и до

                    </p>

                    <input
                        type="range"
                    />

                </div>

                <div
                    className="product__details-wrapper"

                    style={

                        {
                            paddingTop: '15px',
                            display: 'flex',
                            justifyContent: 'space-between'

                        }
                    }
                >
                    <div
                        style={
                            {
                                display: 'flex',
                                gap: '30px',

                            }
                        }
                    >
                        <details
                            style={
                                {
                                    width: '163px',
                                    height: '46px',
                                    padding: '15px',

                                }

                            }
                        >
                            <summary
                                style={
                                    {
                                        fontFamily: 'Gilroy',
                                        fontStyle: 'normal',
                                        fontWeight: '700',
                                        fontSize: '14px',
                                        lineHeight: '17px',
                                        color: '#414D6A',

                                    }
                                }>
                                Состояние
                            </summary>
                        </details>
                        <details
                            style={
                                {
                                    width: '163px',
                                    padding: '15px',
                                    height: '46px',

                                }
                            }
                        >
                            <summary
                                style={
                                    {
                                        fontFamily: 'Gilroy',
                                        fontStyle: 'normal',
                                        fontWeight: '700',
                                        fontSize: '14px',
                                        color: '#414D6A',

                                    }
                                }
                            >

                                Тип объявщика

                            </summary>

                        </details>

                        <details style={

                            {
                                width: '358px',
                                padding: '15px',
                                height: '46px',

                            }

                        }
                        >

                            <summary

                                style={

                                    {
                                        fontFamily: 'Gilroy',
                                        fontStyle: 'normal',
                                        lineHeight: '17px',
                                        fontWeight: '700',
                                        fontSize: '14px',
                                        lineHeight: '17px',
                                        color: '#414D6A',

                                    }
                                }
                            >

                                Ташкент {'>'} Мирза улугбекский район

                            </summary>

                        </details>

                    </div>

                    <button

                        style={

                            {
                                background: '#304FFF',
                                boxShadow: ' 3px 6px 14px #9CC1FF',
                                borderRadius: '22px',
                                textAlign: 'center',
                                color: '#FFFFFF',
                                outline: 'none',
                                border: 'none'

                            }

                        }

                    >
                        <p
                            style={

                                {
                                    width: '151px',

                                    fontFamily: 'Gilroy',
                                    fontStyle: 'normal',
                                    fontWeight: '700',
                                    fontSize: '15px',
                                    lineHeight: '19px',
                                }

                            }

                        >

                            Применить

                        </p>

                    </button>

                </div>

            </div>

            <hr

                style={

                    {

                        borderTop: '1px solid rgba(151, 151, 151, 0.393739)',

                    }

                }
            />

        </>
    )
}

export default HeaderCard