import './items.scss'

import imgOne from './../../assets/icons/1.svg'
import imgTwo from './../../assets/icons/7.svg'
import imgFour from './../../assets/icons/4.svg'
import imgFive from './../../assets/icons/2.svg'
import imgSix from './../../assets/icons/6.svg'
import imgSeven from './../../assets/icons/3.svg'
import imgEight from './../../assets/icons/8.svg'
import imgNine from './../../assets/icons/5.svg'
import imgTen from './../../assets/icons/9.svg'

import { useTranslation } from 'react-i18next'
import { t } from 'i18next'


function items() {
    return (
        <>
            <section className="items">
                <div className="items__wrapper container">
                    <ul className="items__list">
                        <li className="items__item ">
                            <div className='one'>
                                <img src={imgOne} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item1')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='two'>
                                <img src={imgTwo} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item2')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='three'>
                                <img src={imgFour} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item3')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='four'>
                                <img src={imgFive} alt="airplane__svg" />

                            </div>
                            <p className="items__text">{t('items.item4')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='five'>
                                <img src={imgSix} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item5')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='six'>
                                <img src={imgSeven} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item6')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='seven'>
                                <img src={imgEight} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item7')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='eight'>
                                <img src={imgNine} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item8')}</p>
                        </li>
                        <li className="items__item ">
                            <div className='nine'>
                                <img src={imgTen} alt="airplane__svg" />
                            </div>
                            <p className="items__text">{t('items.item9')}</p>
                        </li>
                    </ul>
                </div>
            </section>
        </>
    )
}

export default items