import { Link } from 'react-router-dom'
import './card.scss'

function Card({ id, key, img, name, price, time }) {
    return (
        <div key={key} className="shop__item">
            <Link to={`/product/${id}`}>
                <img className='shop__img' src={img} alt="images" />
                <div>
                    <h2 className='shop__header'>{name}</h2>
                    <p className='shop__time'>{time}</p>
                    <p className='shop__price'>{price}€</p>
                </div>
            </Link>
        </div>
    )
}

export default Card