import './error.scss'
import { Link } from "react-router-dom"

function Error() {
    return (
        <div className="error__body">
            <h2>
            <Link to='/' style={{ color: 'black' }} >
                <div className='error__header' title="404">404</div>
            </Link>
        </h2>
        </div>
    )
}

export default Error