import { NavLink, Outlet } from 'react-router-dom'
import Nested from '../renderFour/render'
import './nestedLink.scss'


function nestedLink() {

    // https://fakestoreapi.com/products?limit=5

    return (
        <>
            <section className='nested'>
                <div className='nested__wrapper container'>
                    <div className='nested__inner top'>
                        <NavLink className="nested__link " end>
                            <p className="nested__text">Просмотренные</p>
                        </NavLink>
                    </div>

                    <div className='nested__inner bottom'>
                        <p className="nested__text">Выбор редакции</p>
                    </div>

                    <div className='nested__inner bottom'>
                        <p className="nested__text">Сниженные цены</p>
                    </div>

                </div>
                <Nested />
            </section>

        </>

    )

}

export default nestedLink