import React, { Component } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default class AutoPlay extends Component {
    render() {
        const settings = {
            dots: true,
            infinite: true,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            speed: 2000,
            autoplaySpeed: 1700,
            cssEase: "linear"
        };
        return (

            <section className='images'>
                <div className="images__wrapper">
                    <div>
                        <Slider {...settings}>
                            <div>
                                <img className='' src={imageOne} />
                            </div>
                            <div>
                                <img className='' src={imageTwo} />

                            </div>
                            <div>
                                <img className='' src={imageFour} />
                            </div>
                            <div>
                                <img className='' src={imageThree} />
                            </div>
                        </Slider>

                    </div>
                </div>
            </section>
        );
    }
}