import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Card from "../../components/card/card"
import './shop.scss'

function Shop() {

    const [info, setInfo] = useState([])

    const params = useParams()

    useEffect(() => {
        fetch("https://fakestoreapi.com/products")
            .then((response) => response.json())
            .then((res) => setInfo(res))
    })
    return (
        <>
            <section className="shop">
                <div className="sshop__wrapper">
                    <div className="container">

                        <div className="shop__wrapper">
                            <ul className="shop__list">
                                <li className="shop__short">
                                    {
                                        info?.slice(0, 16).map(Toxirrrr => (
                                            <Card
                                                id={Toxirrrr.id}
                                                key={Toxirrrr.id}
                                                img={Toxirrrr.image}
                                                time={Toxirrrr.category}
                                                name={Toxirrrr.title}
                                                price={Toxirrrr.price}
                                            />
                                        ))
                                    }
                                </li>
                                <li style={{ display: "none" }} className="shop__all">
                                    {
                                        info?.sort().map(Toxirrrr => (

                                            <Card
                                                id={Toxirrrr.id}
                                                img={Toxirrrr.image}
                                                time='Вчера 22:55'
                                                name={Toxirrrr.title}
                                                price={Toxirrrr.price}
                                            />
                                        ))
                                    }
                                </li>


                            </ul>
                        </div>
                    </div>
                    <div className="shop__btn-wrapper">
                        <details className="shop__more-btn"
                            onClick={() => {
                                document.querySelector(".shop__short").style.display = "none",
                                    document.querySelector(".shop__all").style.display = "flex",
                                    document.querySelector(".shop__all").style.flexWrap = "wrap",
                                    document.querySelector(".shop__more-btn").style.display = "none"
                            }}
                        >
                            <summary>
                                Show more
                            </summary>
                        </details>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Shop

