import i18n from 'i18next'
import { useContext, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import { AuthContext } from '../../components/authContext'
import headerLogo from './../../assets/img/header_logo.svg'
import headerPerson from './../../assets/img/header__person.jpg'
import './header.scss'


function HeaderToken() {

    const ru = useRef()
    const uz = useRef()

    const { token } = useContext(AuthContext)

    const { t } = useTranslation()

    const navigate = useNavigate()

    // if (localStorage.getItem("lng" = ru)) {
    //     // rus.current.style.display = 'flex'
    //     // uzb.current.style.display = 'none'
    // } else {
    //     // uzb.current.className = 'flex'
    //     // rus.current.style.display = 'none'
    // }

    function logOut() {

        setTimeout(function () {
            navigate('/Login')
        }, 2000)

        localStorage.removeItem("token")
    }

    const onChange = (lng) => {
        i18n.changeLanguage(lng.target.value)
        const save = lng.target.value
        localStorage.setItem('lng', (save))
        // localStorage.setItem('itemLang',  JSON.stringify(lng))
    }

    const langToken = JSON.parse(localStorage.getItem('lang'))
    const [lang, setLang] = useState(langToken)

    useEffect(() => {
        if (lang) {
            return localStorage.setItem('token', JSON.stringify(lang))
        }

        localStorage.removeItem('lang')
    }, [lang])



    if (localStorage.getItem("lng" && ru)) {
    } else {
    }

    localStorage.getItem("lngItem")

    return (
        <>

            <header>

                <div className='container'>

                    <div className="header__wrapper">

                        <div className="header__wrapper-right">
                            <Link to='/'>
                                <div className='header__left-wrapper'>

                                    <img src={headerLogo} alt="kiwi Logo" />

                                    <p>{t('header.headerHeader')}</p>

                                </div>
                            </Link>


                            <ul className='header__list' style={{ padding: '0', listStyleType: 'none' }}>

                                <li className='header__item'><NavLink className='header__item' to={'/products'}>{t('header.headerOne')}</NavLink></li>
                                <li className='header__item'><NavLink className='header__item' to={'/add-product'}>{t('header.headerTwo')}</NavLink></li>
                                <li className='header__item'><NavLink className='header__item' to={'/busines'}>{t('header.headerThree')}</NavLink></li>
                                <li className='header__item'><NavLink className='header__item' to={'/Help'}>{t('header.headerFour')}</NavLink></li>

                            </ul>


                        </div>
                        <div className='header__right-wrapper'>

                            <form className={token ? 'header__form-token' : 'header__form'} onChange={onChange}>


                                <input value='uz' ref={uz} className='header__lang uz' id='uz' type="radio" name='lang' />
                                <label htmlFor='uz'>O’z</label>

                                <span>|</span>

                                <input className='header__lang ru' value='ru' ref={ru} id='ru' type="radio" name='lang' />
                                <label htmlFor='ru'>Рус</label>

                            </form>
                            {
                                token ? (
                                    <div className="header__login">
                                        <Link to='/profile' className='header__login-wrapper' style={{ display: 'flex', alignItems: 'center' }}>
                                            <p
                                                style={
                                                    {
                                                        color: '#787993',
                                                        marginRight: '13px',
                                                        fontWeight: '500',
                                                        fontSize: '17px',
                                                        lineHeight: '21px',
                                                    }
                                                }
                                            >Темиров</p>
                                            <img className='header__img' src={headerPerson} alt="human__photo" />
                                            <button className='header__btn' onClick={logOut}>Log Out</button>
                                        </Link>
                                    </div>
                                ) : (
                                    <div className="header__login">
                                        <Link to='/Login'>
                                            {t('header.login')} |
                                        </Link>
                                        <pre> {t('header.reg')}</pre>

                                    </div>
                                )
                            }
                        </div>

                    </div>

                </div>

            </header >

        </>
    )
}

export default HeaderToken