import { NavLink, Outlet } from 'react-router-dom'
import './act.scss'

function act() {
    return (
        <>
            <section className="act">
                <div className='act__wrapper container'>

                    <div className='act__inner top'>
                        <NavLink className="act__link " to='/' end>
                            <p className="act__text">Новые объявления</p>
                        </NavLink>
                    </div>

                    <div className='act__inner bottom'>
                        <NavLink className="act__link " to='/best-products'>
                            <p className="act__text">Лучщие предложение</p>
                        </NavLink>
                    </div>
                </div>
            </section>
            <Outlet />
        </>
    )
}

export default act