import { NavLink, Outlet, useNavigate } from 'react-router-dom'
import { AuthContext } from './../../components/authContext'
import inputImg from './../../assets/icons/input__img.svg'
import btnOne from './../../assets/icons/btn1.svg'
import './profile.scss'
import { useContext, useEffect } from 'react'

function Profile() {

  const { token } = useContext(AuthContext)

  const navigate = useNavigate()

  useEffect(() => {
    {
      token ? (
        console.log('Token✅')
      ) : (
        console.log('Token❌'),
        navigate('/')
      )
    }
  }, [token])


  return (
    <>
      <section className="profile">
        <div className='container'>

          <div className="profile__wrapper">
            <div className="profile__inner">
              <div className="profile__top">
                <div className="profile__top-wrapper">
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div>
                      <select className="profile__select one">
                        <option value="one">Любая категория</option>
                        <option value="one">1</option>
                        <option value="two">2</option>
                        <option value="three">3</option>
                        <option value="one">4</option>
                      </select>
                      <input className='profile__input' type="search" defaultValue={'Что будем искать ?'} style={{ width: '358px' }} />
                      <select className="profile__select three">
                        <option value="one">По всей Узбекистану</option>
                        <option value="one">1</option>
                        <option value="two">2</option>
                        <option value="three">3</option>
                        <option value="one">4</option>
                      </select>
                      <button className='profile__btn' type="submit">Найти</button>
                    </div>
                    <img src={inputImg} />
                  </div>
                  <div className="profile__name"
                    style={
                      {
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        paddingTop: '31px'

                      }
                    }>
                    <p>Здравствуйте Зухриддин Темиров</p>
                    <div
                      className="profile__text"
                      style={
                        {
                          display: 'flex',
                          gap: '26px',
                          alignItems: 'center',

                        }
                      }
                    >
                      <p>Ваш счет: 2 3000 сум </p>
                      <img src={btnOne} alt="button" />
                    </div>
                  </div>
                </div>
                <div className="profile__link-wrapper">
                  <ul className="profile__list"
                    style={
                      {
                        display: 'flex',
                        gap: '80px'
                      }
                    }
                  >
                    <li className="profile__item"
                      style={
                        {
                          fontFamily: 'Gilroy',
                          fontStyle: 'normal',
                          fontWeight: '500',
                          fontSize: '17px',
                          lineHeight: '21px',
                          textAlign: 'center',
                          color: '#304FFF',
                          paddingTop: '16px',
                          paddingBottom: '14px'

                        }
                      }
                    >
                      <NavLink end to='/profile/'>Объявления</NavLink>
                    </li>
                    <li className="profile__item"
                      style={
                        {
                          fontFamily: 'Gilroy',
                          fontStyle: 'normal',
                          fontWeight: '500',
                          fontSize: '17px',
                          lineHeight: '21px',
                          textAlign: 'center',
                          color: '#304FFF',
                          paddingTop: '16px',
                          paddingBottom: '14px'
                        }
                      }
                    >
                      <NavLink to='/profile/messages'>Сообщения</NavLink>
                    </li>
                    <li className="profile__item"
                      style={
                        {
                          fontFamily: 'Gilroy',
                          fontStyle: 'normal',
                          fontWeight: '500',
                          fontSize: '17px',
                          lineHeight: '21px',
                          textAlign: 'center',
                          color: '#304FFF',
                          paddingTop: '16px',
                          paddingBottom: '14px'
                        }
                      }
                    >
                      <NavLink to='Payments'>Платежи и счёт</NavLink>
                    </li>
                    <li className="profile__item"
                      style={
                        {
                          fontFamily: 'Gilroy',
                          fontStyle: 'normal',
                          fontWeight: '500',
                          fontSize: '17px',
                          lineHeight: '21px',
                          textAlign: 'center',
                          color: '#304FFF',
                          paddingTop: '16px',
                          paddingBottom: '14px'
                        }
                      }
                    >
                      <NavLink to='Settings'>Настройки</NavLink>
                    </li>
                    <li className="profile__item"
                      style={
                        {
                          fontFamily: 'Gilroy',
                          fontStyle: 'normal',
                          fontWeight: '500',
                          fontSize: '17px',
                          lineHeight: '21px',
                          textAlign: 'center',
                          color: '#304FFF',
                          paddingTop: '16px',
                          paddingBottom: '14px'
                        }
                      }
                    >
                      <NavLink to='my-business'>Мой бизнес</NavLink>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <Outlet />
        </div>
      </section>
    </>
  )
}

export default Profile