import Shop from "./../shop/shop";
import './product.scss'
import HeaderCard from "../header-bottom/headerBottom";
import Nested from "../renderFour/render";
import img from './../../assets/icons/all.svg'

function Product() {

    return (
        <>
            <HeaderCard />
            <Shop />
            <div className="container"
                style={
                    {
                        userSelect: 'none'
                    }
                }>
                <img src={img} alt="images"
                    style={
                        {
                            userSelect: 'none'
                        }
                    }
                />

                <h2 style=
                    {
                        {
                            color: '#41D481',
                            marginTop: '40px',
                        }
                    }
                >
                    САМОЕ ИНТЕРЕСНОЕ
                </h2>
            </div>

            <Nested
                style={
                    {
                        marginBottom: 'none'
                    }
                }
            />

        </>
    )
}

export default Product